.footer__author {
  margin: 0;
  font-family: "Inter", Arial, Helvetica, sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  color: #545454;
}

@media screen and (max-width: 450px) {
  .footer__author {
    max-width: 282px;
    margin: 0 auto;
    font-size: 14px;
    line-height: 17px;
  }
}
